<template>
	<div class="personal-content-body jgInfo">
		<div class="table-content">
			<div class="clear table-content-title">
				<!-- <div class="left">机构信息</div> -->
			</div>
			<div class="table-group">
				<div class="table-group-content">
					<div class="table-group-tab">
						<div class="clear"></div>
					</div>
					<div class="info_line">
						<span class="info_line_key">机构logo</span>
						<label for="upload-img-input">
                          <div class="tx_box">
                              <img :src="logo==''?'../../static/images/homepage_txsc.png':logo" alt="" @error="getImgurl">
                              <span>修改logo</span>
                          </div>
                        </label>
						<span class="editor_btn" @click="toEditor" v-if="!isEditor"><i class="iconfont icon-bianji"></i>编辑</span>
					</div>
					<input type="file" v-on:change="changeToop($event)" ref="inputer" id="upload-img-input" style="display:none" />
					<div class="info_line info_line1">
						<span class="info_line_key">机构全称</span>
						<span class="info_line_value" v-if="!isEditor">{{mechanism_name==''?'未知':mechanism_name}}</span>
						<el-input placeholder="机构全称" v-model="mechanism_name" class="header-right-time right" :clearable='true' v-else @change="changeValue">
						</el-input>
					</div>
					<div class="info_line info_line1">
						<span class="info_line_key">机构规模</span>
						<span class="info_line_value" v-if="!isEditor">{{scaleValue==''?'未知':scaleValue}}</span>
						<el-select v-model="scale" placeholder="机构规模" class="header-right-time right" v-else @change="changeValue">
							<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
							</el-option>
						</el-select>
					</div>
					<div class="info_line" v-if="isEditor">
						<span class="info_line_key"></span>
						<div>
							<button @click="toCancle">取消</button>
							<button @click="toCommit">完成</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { mechanismInfo, upd_mechanism_info, addlogo } from "../../config/api";
	import { dialogConfirm, dialogConfirmHasbtn } from "../../config/mUtils";
	export default {
		data() {
			return {
				options: [{
						value: 1,
						label: "少于10人"
					},
					{
						value: 2,
						label: "10～30人"
					},
					{
						value: 3,
						label: "30～50人"
					},
					{
						value: 4,
						label: "50～100人"
					},
					{
						value: 5,
						label: "100人以上"
					}
				],
				scale: 1,
				scaleValue: "",
				mechanism_name: "",
				logo: "",
				isEditor: false,
				needSave: false
			};
		},
		created() {
			this.getInfo();
		},
		computed: {},
		mounted() {},
		methods: {
			getImgurl(e) {
				e.currentTarget.src = "../../static/images/homepage_txsc.png";
				e.onerror = null;
			},
			getInfo() {
				mechanismInfo().then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res);
						let data = res.data.payload;
						this.mechanism_name = data.mechanism_name;
						this.scale = data.scale;
						this.logo = data.logo;
						this.options.forEach(element => {
							if(element.value == data.scale) {
								this.scaleValue = element.label;
							}
						});
						this.scale = data.scale;
					} else {
						dialogConfirm("info", res.data.message);
					}
				});
			},
			changeToop(e) {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/.(gif|jpg|jpeg|png)$/.test(file.type)) {
					dialogConfirm("info", "上传头像图片只能是jpg/png/gif格式");
					inputDOM.value = "";
					return false;
				}
				addlogo(file).then(res => {
					if(res.data.msgNo == 1) {
						//   console.log(res);
						dialogConfirm("success", "logo修改成功");
						inputDOM.value = "";
						this.logo = res.data.payload;
					}
				});
			},
			changeValue() {
				this.needSave = true;
			},
			toEditor() {
				this.isEditor = true;
			},

			toCancle() {
				if(this.needSave) {
					dialogConfirmHasbtn("确定不保存退出么？")
						.then(() => {
							this.getInfo();
							this.isEditor = false;
						})
						.catch(() => {});
				} else {
					this.isEditor = false;
				}
			},
			toCommit() {
				if(this.mechanism_name == '') {
					dialogConfirm('info', '机构全称不能为空!');
					return false;
				}
				upd_mechanism_info({
					mechanism_name: this.mechanism_name,
					scale: this.scale
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '修改成功');
						this.getInfo();
						this.isEditor = false;
					} else {
						dialogConfirm("info", res.data.message);
					}
				});
			}
		}
	};
</script>

<style lang="scss" scoped>
	@import "../../style/modal.scss";
	.table-group-content {
		min-height: 500px;
	}
	
	.editor_btn {
		cursor: pointer;
		font-size: 12px;
		color: #4a4a4a;
		position: relative;
		left: 80px;
		top: -40px;
	}
	
	.icon-bianji {
		color: #aeb1b6;
		position: relative;
		top: 2px;
		margin-right: 4px;
	}
	
	.editor_btn:hover {
		.icon-bianji {
			color: #bed7f5;
		}
		color: #4a90e2;
	}
	
	.info_line {
		display: flex;
		align-items: center;
		margin-bottom: 35px;
		padding-left: 50px;
		.info_line_key {
			font-size: 14px;
			color: #6f7071;
			width: 120px;
		}
		.info_line_value {
			font-size: 14px;
			color: #35383a;
		}
		button {
			font-size: 14px;
			border: 1px solid #3c98e8;
			text-align: center;
			padding: 3px 19px;
			margin-right: 20px;
		}
		button:nth-child(1) {
			color: #3c98e8;
		}
		button:nth-child(2) {
			color: #ffffff;
			background-color: #3c98e8;
		}
	}
	
	.info_line1 {
		height: 30px;
	}
	
	.tx_box {
		display: flex;
		flex-direction: column;
		align-items: center;
		cursor: pointer;
		img {
			width: 80px;
			height: 80px;
			border-radius: 50%;
		}
		span {
			font-size: 14px;
			color: #3c98e8;
			margin-top: 5px;
		}
	}
	
	/*新加样式*/
	.table-content{
		padding: 7px 30px;
	}
</style>


